html, body {

  width: 100%;
  height: 100%;
  position: relative;
  font-family: "Open Sans";
}

body {
  background-size: cover;
  font-size: 100%;
  width: 100%;
}



.AppTitle {
  text-align: center;
  background-color: #bdb1b3;

  font-size: 110%;
  /* padding: 1em; */
  color: black;
  padding: 0.8em 0;
}



      /* container layout begin */

.pageComponents {

  display: -ms-inline-flexbox;

  display: inline-flex;
  overflow: hidden;
  height: 100%;
  position: fixed;
  width: 100%;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;

}

.noteListContainer {
  position: relative;

  margin: 1em 0em;
  padding: 1em 0em 1em 0em;
  -ms-flex-preferred-size: 30%;
      flex-basis: 30%;
  text-align: left;
  /* overflow-y: auto;
  overflow-x: hidden;
  height: 70vh; */
  -ms-flex-order: 1;
      order: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;



}

.editorContainer {
  margin: 0em;
  padding: 2em;
  border-left: #f6f7fb solid 2px;
  color: #2C3A51;
  text-align: left;
  position: relative;
  overflow-y: hidden;
  overflow-x: hidden;
  -ms-flex-order: 2;
      order: 2;
  height: 100vh;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  -ms-flex-preferred-size: 60%;
      flex-basis: 60%;
  min-width: 60%;
  max-width: 100%;
  background: #FBFAFA;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-item-align: stretch;
      align-self: stretch
}

/* container layout end */


/* note list style begin  */

.note-list-header {
  font-size: 20px;
  text-align: center;
  padding: 1.5em 1em;
  margin: 0.75em;
  color: #585656;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  -ms-flex-align: center;
      align-items: center

}

.note-list {
  position: relative;
  overflow-y: auto;
  overflow-x: hidden;
  height: 68vh;
  margin: 0.5em 0 0.5em 0.5em;
}

.note-list-item {
  padding: 1.1em;
  font-size: 13px;
  letter-spacing: .5px;
  border-bottom: #f5efef solid 1px;
  min-height: 24px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  color: #484545;
}

.note-list-item:hover {
  background: #FBF1F1;
}

/* note list style end  */


.aboveEditor {
  font-size: 1.25em;
  text-align: left;

}

input[type="text"] {
  font-size: 14.5px;
  line-height: 20px;
  height: 20px;
  letter-spacing: 0.7px;
  font-family: "Open Sans";
  border-bottom: #f6f7fb solid 2px;
  border-top: none;
  border-left: none;
  border-right: none;
  width: 90%;
  position: inline;
  padding: 0.5em;
  margin-left: 0.6em;
  margin-bottom: 0.6em;
}

.tool-bar {
  border: #f6f7fb solid 2px;
  text-align: center;
  font-family: 'IBM Plex Sans', sans-serif;
  background-color: #dedcdc;
  margin: 0.5em 0.5em 0em 0.5em;
  padding: 0.5em;
  border-radius: 4px;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,.1);
          box-shadow: 0 0 5px rgba(0,0,0,.1);
}

.editors {
  border: none;
  border-radius: 4px;
  padding: 1em 1.5em 3em 1.5em;
  margin: -0.2em 0.6em 1em 0.6em;
  font-family: "Helvetica";
  line-height: 1.4;
  color: #2C3A51;
  font-size: 16px;
  text-align: left;
  background-color: white;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,.1);
          box-shadow: 0 0 5px rgba(0,0,0,.1);
  min-height: 36%;
  overflow-x:hidden;
  overflow-y:auto;
  position: relative;
  height: 40vh;
  font-size: 90%;

}

.editors a {
  color: blue;
}

a {
  color: blue;
}

a:visited {
  color: blue;
}

img {
  min-width: 100px;
  max-width: 300px;
  text-align: center;
}


/* button styles begin  */

/*
button#new,  {
  padding: 0.25em;
  border-radius: 100px;
  width: 2em;
}*/

.belowEditor
{
  float: right;
}

.btHTML, .btJS {
  border-radius: 10px;
  font-size: 13px;
  display: inline;
  padding: 0.5em;
  color: white;
  border: none;
  background-color:#5356ff;
  margin: 0.35em;
}

.code-block, div.code-block {
  color: #90caf9;
  line-height: 1.2;
  padding: 3px;
  margin-left: 4px;
  background-color: black;
  font-family: "Andale Mono";
  font-size: 13px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  white-space: pre-wrap;
  display: block;

}



.styleButton, #link_url, .RichEditor-styleButton {
  border-radius: 7px;
  padding: 6px 12px;
  background: white;
  border: none;
  color: #5356ff;
  margin: 0.35em;
  max-width: 35px;
  min-width: 30px;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 13px;
  height: 2.25em;
}

.RichEditor-activeButton {
  background-color: #feb47b;
  color: white;
}

select {
  padding: 0.5em;
  background: white;
  border: none;
  color: #5356ff;
  margin: 0.35em;
  border-radius: 6px;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 13px;;
  height: 2.25em;
}

#bold, #italic, #underline {
  width: 30px;
}

#code {
  font-family: "Andale Mono", monospace;
  padding-left: 3px !important;
  padding-right: 3px !important;
}

.styleButton#bold {
  font-weight: bold;
}

.styleButton#italic {
  font-style: italic;
  font-family: 'IBM Plex Sans', sans-serif;
}

.styleButton#underline {
  text-decoration: underline;
}

.material-icons {
  font-size: 13px;
}

textarea {
  width: 80%;
  height: 120px;
  padding: 12px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  resize: none;
}


/* button styles end */



